.recipe-edit {
  position: fixed;
  right: 0;
  top: 0;
  width: 50%;
  max-height: 100%;
  overflow-y: auto;
  padding: 30px;
  padding-top: 10px;

  .remove-button-container {
    text-align: end;

    .remove-button {
      padding: 0;
      font-size: 1.5rem;
    }
  }

  .details-grid {
    display: grid;
    grid-template-columns: auto 1fr;
    row-gap: 10px;
    column-gap: 40px;
  }

  .label {
    font-weight: bold;
  }

  .input {
    border-radius: 5px;
    border: 1px solid black;
    font-size: inherit;
    padding: 5px 10px;
    outline: none;
    width: 100%;
  }

  textarea {
    resize: none;
    height: 200px;
  }

  .ingredient-grid {
    display: grid;
    grid-template-columns: repeat(3, auto);
    gap: 10px;
    margin-top: 20px;
    margin-left: 40px;
  }

  .add-ingredient-btn-container {
    margin-top: 20px;
    text-align: center;
  }
}

@media (max-width: 1000px) {
  .recipe-edit {

    .details-grid, .ingredient-grid {
      grid-template-columns: 1fr;
    }
  }
}